import { Info } from "@mui/icons-material";
import {
  Typography,
  Grid,
  Container,
  ImageList,
  ImageListItem,
  ImageListItemBar,
  IconButton,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import Typed from "react-typed";
import Wheat from "../../images/wheat.jpg?format=webp";
import { FoodGrade, IndustrialGrade, ProductCategories } from "./utils";
import type { IconButtonProps } from "@mui/material";

function ListItemBarContent(input: ProductCategories): JSX.Element {
  return (
    <>
      <Typography variant="subtitle2" sx={{ whiteSpace: "normal" }}>
        Uses: {input.uses}
      </Typography>
      <Typography variant="subtitle2" sx={{ whiteSpace: "normal" }}>
        Functions: {input.functions}
      </Typography>
      <Typography variant="subtitle2" sx={{ whiteSpace: "normal" }}>
        Properties: {input.properties}
      </Typography>
    </>
  );
}

export function Home(): JSX.Element {
  const { t } = useTranslation();
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down("sm"));

  const checkedInitialDictionary = [...IndustrialGrade, ...FoodGrade].reduce<
    Record<string, boolean>
  >((acc, { image }) => ({ ...acc, [image.src]: false }), {});

  const [isClickedDictionary, setIsClickedDictionary] =
    useState<Record<string, boolean>>(checkedInitialDictionary);

  const handleClick: IconButtonProps["onClick"] = (event) => {
    const id = event.currentTarget.getAttribute("data-id") ?? "";
    setIsClickedDictionary((prev) => ({
      ...prev,
      [id]: !prev[id],
    }));
  };

  return (
    <>
      <Container
        sx={{
          backgroundImage: `url('${Wheat}')`,
          height: "60vh",
          backgroundPositionY: "10%",
          width: "100%",
          backgroundColor: "transparent",
          borderRadius: "15px",
          backgroundSize: "cover",
        }}
        maxWidth="xl"
      >
        <Grid container justifyContent="center" alignContent="center" height="100%">
          <Typography variant="h1">
            <Typed
              strings={["Guar Gum Powder", "Guar Splits", "Guar Meal", "Guar Protein"]}
              typeSpeed={40}
              backSpeed={50}
              backDelay={2000}
              loop
              style={{ color: "#FFFFF0", fontWeight: 500 }}
            />
          </Typography>
        </Grid>
      </Container>
      <Container sx={{ marginTop: 2 }} maxWidth="xl">
        <Grid container spacing={2}>
          <Grid container item>
            <Typography variant="h6">About Us</Typography>
          </Grid>
          <Grid container item>
            <Typography variant="body1">{t("IntroA")}</Typography>
          </Grid>
          <Grid container item>
            <Typography variant="body1">{t("IntroB")}</Typography>
          </Grid>
          <Grid container item>
            <Typography variant="body1">{t("IntroC")}</Typography>
          </Grid>
          <Grid container item>
            <Typography variant="h6">Industrial Grade Guar Applications</Typography>
          </Grid>
          <Grid container item>
            {matches === true ? (
              <ImageList cols={1} gap={10}>
                {IndustrialGrade.slice(0, 4).map(
                  ({ industry, image: { width, height, src, srcSet } }, index) => (
                    <ImageListItem key={src}>
                      <img {...{ width, height, src, srcSet }} alt={industry} loading="lazy" />
                      <ImageListItemBar
                        title={industry}
                        subtitle={
                          isClickedDictionary[src] === true ? (
                            <ListItemBarContent {...IndustrialGrade[index]} />
                          ) : undefined
                        }
                        position="top"
                        actionIcon={
                          <IconButton
                            sx={{
                              position: "absolute",
                              top: "5px",
                              right: "10px",
                            }}
                            color="primary"
                            onClick={handleClick}
                            data-id={src}
                          >
                            <Info />
                          </IconButton>
                        }
                      />
                    </ImageListItem>
                  )
                )}
              </ImageList>
            ) : (
              <ImageList sx={{ width: "100%" }} variant="quilted" cols={4} gap={10} rowHeight={150}>
                {IndustrialGrade.map(
                  ({ industry, image: { width, height, src, srcSet }, cols, rows }, index) => (
                    <ImageListItem cols={cols ?? 1} rows={rows ?? 1} key={src}>
                      <img {...{ width, height, src, srcSet }} alt={industry} loading="lazy" />
                      <ImageListItemBar
                        title={industry}
                        subtitle={
                          isClickedDictionary[src] === true ? (
                            <ListItemBarContent {...IndustrialGrade[index]} />
                          ) : undefined
                        }
                        position="top"
                        actionIcon={
                          <IconButton
                            sx={{
                              position: "absolute",
                              top: "5px",
                              right: "10px",
                            }}
                            color="primary"
                            onClick={handleClick}
                            data-id={src}
                          >
                            <Info />
                          </IconButton>
                        }
                      />
                    </ImageListItem>
                  )
                )}
              </ImageList>
            )}
          </Grid>
          <Grid container item>
            <Typography variant="h6">Food Grade Guar Applications</Typography>
          </Grid>
          <Grid container item>
            {matches === true ? (
              <ImageList cols={1} gap={10}>
                {FoodGrade.slice(0, 4).map(
                  ({ industry, image: { width, height, src, srcSet } }, index) => (
                    <ImageListItem key={src}>
                      <img {...{ width, height, src, srcSet }} alt={industry} loading="lazy" />
                      <ImageListItemBar
                        title={industry}
                        subtitle={
                          isClickedDictionary[src] === true ? (
                            <ListItemBarContent {...FoodGrade[index]} />
                          ) : undefined
                        }
                        position="top"
                        actionIcon={
                          <IconButton
                            sx={{
                              position: "absolute",
                              top: "5px",
                              right: "10px",
                            }}
                            color="primary"
                            onClick={handleClick}
                            data-id={src}
                          >
                            <Info />
                          </IconButton>
                        }
                      />
                    </ImageListItem>
                  )
                )}
              </ImageList>
            ) : (
              <ImageList sx={{ width: "100%" }} variant="quilted" cols={4} gap={10} rowHeight={150}>
                {FoodGrade.map(
                  ({ industry, image: { width, height, src, srcSet }, cols, rows }, index) => (
                    <ImageListItem cols={cols ?? 1} rows={rows ?? 1} key={src}>
                      <img {...{ width, height, src, srcSet }} alt={industry} loading="lazy" />
                      <ImageListItemBar
                        title={industry}
                        subtitle={
                          isClickedDictionary[src] === true ? (
                            <ListItemBarContent {...FoodGrade[index]} />
                          ) : undefined
                        }
                        position="top"
                        actionIcon={
                          <IconButton
                            sx={{
                              position: "absolute",
                              top: "5px",
                              right: "10px",
                            }}
                            color="primary"
                            onClick={handleClick}
                            data-id={src}
                          >
                            <Info />
                          </IconButton>
                        }
                      />
                    </ImageListItem>
                  )
                )}
              </ImageList>
            )}
          </Grid>
        </Grid>
      </Container>
    </>
  );
}
